.vetskitchen-image-text-content {
  min-height: 200px;
  color: #000000;
}
.vetskitchen-image-text-content .col-image .image {
  object-fit: cover;
  width: 100%;
  height: auto;
}
.vetskitchen-image-text-content .col-image .subtext {
  padding-top: 40px;
  padding-right: 30px;
  font-size: 18px;
  color: #a9a9a9;
  text-align: right;
}
.vetskitchen-image-text-content .acf-innerblocks-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 30px 0;
  height: 100%;
  text-align: center;
}
.vetskitchen-image-text-content .acf-innerblocks-container > *:not(.wp-block-heading) {
  margin-right: auto;
  margin-left: auto;
  max-width: 420px;
}
.vetskitchen-image-text-content.alignfull .acf-innerblocks-container > *:not(.wp-block-heading) {
  max-width: 576px;
}
.vetskitchen-image-text-content .image-right .col-content {
  order: 1;
}
.vetskitchen-image-text-content .image-right .col-image {
  order: 2;
}
.vetskitchen-image-text-content .wp-block-buttons {
  margin-top: 20px;
}
.vetskitchen-image-text-content .row.g-0 .col-image {
  display: flex;
}
.vetskitchen-image-text-content .row.g-0 .col-image .wrapper-image {
  width: 100%;
}
.vetskitchen-image-text-content .row.g-0 .col-image .image {
  height: 100%;
}
@media screen and (min-width: 768px) {
  .vetskitchen-image-text-content.alignfull .image-left {
    margin-right: 0;
  }
  .vetskitchen-image-text-content.alignfull .image-right {
    margin-left: 0;
  }
}
@media screen and (min-width: 1200px) {
  .vetskitchen-image-text-content.alignfull:not(.fullwidth-text) .image-left .col-content {
    padding-right: calc((100vw - 1176px) / 2);
  }
  .vetskitchen-image-text-content.alignfull:not(.fullwidth-text) .image-right .col-content {
    padding-left: calc((100vw - 1176px) / 2);
  }
}
@media screen and (max-width: 767px) {
  .vetskitchen-image-text-content .col-content {
    order: 1;
  }
  .vetskitchen-image-text-content .col-image {
    order: 2;
  }
  .vetskitchen-image-text-content .col-image .subtext {
    padding: 40px 15px 0;
    color: #a9a9a9;
  }
  .vetskitchen-image-text-content .acf-innerblocks-container {
    padding: 50px 12px;
  }
  .vetskitchen-image-text-content.alignfull .acf-innerblocks-container {
    padding-right: 24px;
    padding-left: 24px;
  }
}